import React from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import PersonAdd from '@mui/icons-material/PersonAddAltTwoTone';
import FolderSharedTwoToneIcon from '@mui/icons-material/FolderSharedTwoTone';
import EditTwoToneIcon from '@mui/icons-material/EditTwoTone';
import DeleteTwoToneIcon from '@mui/icons-material/DeleteTwoTone';
import SupervisorAccountTwoToneIcon from '@mui/icons-material/SupervisorAccountTwoTone';
import AssignmentRoundedIcon from '@mui/icons-material/AssignmentTwoTone';
import { Fab, IconButton, Tooltip } from '@mui/material';
import { accessID, GetAccessLevelName } from 'utils/accessLevels.util';
import { ShowUpAnimated } from 'components/animated/showUp';

export function ListView({ admins, onSelect, onDelete, assingTask, myAccess }) {
  return (
    <div className="row">
      <div className="col-12">
        <div className="row">
          <div className="col-6 p-3">
            <Typography variant="h5">
              <SupervisorAccountTwoToneIcon fontSize="large" className="mx-2" />
              ادمین ها
            </Typography>
          </div>
          <div className="col-6 px-5 py-2 text-start">
            <Fab
              size="small"
              color="primary"
              onClick={() => {
                onSelect('');
              }}>
              <PersonAdd />
            </Fab>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            {admins &&
              admins.map((item, index) => (
                <Accordion key={`admin-${index}-${item._id}`}>
                  <AccordionSummary
                    key={`admin-${index}-${item._id}`}
                    expandIcon={
                      <ShowUpAnimated right delay={index / 4}>
                        <ExpandMoreIcon color="secondary" />
                      </ShowUpAnimated>
                    }>
                    <ShowUpAnimated delay={index / 4}>
                      <Typography variant="body2" color={'secondary'}>
                        <FolderSharedTwoToneIcon className="mx-2" /> {item.Username}
                      </Typography>
                    </ShowUpAnimated>
                  </AccordionSummary>
                  <AccordionDetails>
                    <div className="row">
                      <div className="col-12">
                        <Typography variant="body2">
                          سطح دسترسی : {GetAccessLevelName(item.Access)}
                        </Typography>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-12">
                        <Tooltip title={'حذف ' + item.Username}>
                          <IconButton
                            color="error"
                            disabled={myAccess !== accessID.manager}
                            onClick={() => {
                              onDelete(item._id);
                            }}>
                            <DeleteTwoToneIcon />
                          </IconButton>
                        </Tooltip>
                        <Tooltip title={'ویرایش ' + item.Username}>
                          <IconButton
                            color="primary"
                            disabled={myAccess !== accessID.manager}
                            onClick={() => {
                              onSelect(item._id);
                            }}>
                            <EditTwoToneIcon />
                          </IconButton>
                        </Tooltip>
                        <Tooltip title={'تعریف تسک برای ' + item.Username}>
                          <IconButton
                            onClick={() => {
                              assingTask(item._id);
                            }}>
                            <AssignmentRoundedIcon color="secondary" />
                          </IconButton>
                        </Tooltip>
                      </div>
                    </div>
                  </AccordionDetails>
                </Accordion>
              ))}
          </div>
        </div>
      </div>
    </div>
  );
}
export default ListView;
